export default {
	data() {
		return {
			ordernumber: "2132134564646",
			type: '95#乙醇汽油',
			mode: "配送",
			title: "选择单号",
			url: '',
			page: 1,
			pageSize: 10,
			orderInfo: [],
		}
	},



	methods: {
		getorderNumberInfo() {
			this.test.post('order/query_orderSnInfo', {
				page: this.page,
				pageSize: this.pageSize,
			}).then(res => {
				console.log(res)
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					this.orderInfo = res.data.value
				}
			}).catch(err => {
				console.log(err)
			})
		},
		getOrderNumber() {
			uni.navigateTo({
				url: "../" + this.url + "?ordernumber=" + this.ordernumber
			})
		}
	},
	onLoad(option) {
		this.url = option.url;
		this.getorderNumberInfo()
	}
}
